

.main {
  flex-direction: column;
  height: 100%;
  display: flex;
 
  box-sizing: border-box;
  width: 100%;
  .top{
    width: 100%;
    box-sizing: border-box;
    
    padding: 0.5rem 1rem;
    display: flex;
 
   
  }
  .content {
    box-sizing: border-box;
    flex-direction: row;
    display: flex;
    width: 100%;
    flex: 1;
  }
}

 